import React, { useState } from "react";
import axios from "axios";
import Button from 'react-bootstrap/Button';
import Navbarpage from "./navpage.js";

function Register() {

	const [checkNewId, setCheckID] = useState()
	const [validId, setValidId] = useState(null)

	const [checkNewPw, setCheckPw] = useState()
	const [checkNewPw2, setCheckPw2] = useState()

	const [studentId, setStudentId] = useState()
	const [studentName, setStudentName] = useState()
	const [phonenum, setPhonenum] = useState()

	const checkID = async () => {
		console.log('  : ', checkNewId)
		if (!checkNewId) {
			setValidId(null)
			console.log('') //   validId null 
		} else {
			await axios.get('http://localhost:8081/checkid', { params: { checkNewId } }).then((result) => {
				console.log('result.data : ', result.data)

				if (result.data === checkNewId) {
					setValidId(false)
					console.log('', result.data, checkNewId, validId)
				}
				else {
					setValidId(true)
					console.log('', result.data, checkNewId, validId)
				}
			}).catch(() => {
				console.log('failed')
			})
		}
	}




	return (
		<div>
			<Navbarpage />
			<div className="registerpage">
				<div className="registertitle">
					
				</div>
				<hr className="divider" />
				<div className="registercontent">
					<form action="http://localhost:8081/newregister" method="POST">
						<div className="registersubtitle">
							
						</div>
						<div className="registerid">
							<input name="userid" type="text" value={checkNewId}
								className="registerinput" placeholder="ID ."
								onChange={(e) => {
									setCheckID(e.target.value)
									console.log(checkNewId)
								}}
							/>
							<button type="button" className="btnCss2" onClick={checkID}></button>
						</div>
						{
							validId === null
								? <div className="needuseid"> </div>
								: (validId === false ?
									<div className="cantuseid">  .</div>
									: <div className="canuseid">   .</div>
								)
						}
						<div className="registerpw">
							<div className="registersubtitle">
								
							</div>
							<div className="registerpw1">
								<input name="newpassword" className="registerinput" placeholder="PW ." type="password"
									onChange={(e) => {
										setCheckPw(e.target.value)
										console.log('  password: ', checkNewPw)
									}}
								/>
							</div>
							<div className="registerpw2">
								<input name="checknewpassword" className="registerinput" placeholder="!" type="password"
									onChange={(e) => {
										setCheckPw2(e.target.value)
										console.log('  password: ', checkNewPw2)
									}}
								/>
							</div>
							{
								!checkNewPw2
									? <div className="needuseid"> </div>
									: (checkNewPw === checkNewPw2 ?
										<div className="canuseid"> .</div>
										: <div className="cantuseid"> .</div>
									)
							}
							<hr className="divider" />
							<div className="registersubtitle">
								, 
							</div>
							<div className="registerpw2">
								<input name="studentid" className="registerinput" placeholder=""
									onChange={(e) => {
										setStudentId(e.target.value)
										console.log(' : ', studentId)
									}}
								/>
								<input name="studentname" className="registerinput" placeholder=""
									onChange={(e) => {
										setStudentName(e.target.value)
										console.log(' : ', studentName)
									}}
								/>
							</div>
							<div className="registersubtitle">
								
							</div>
							<div className="registerpw2">
								<input name="phonenum" className="registerinput" placeholder="ex) 010-1234-1234"
									onChange={(e) => {
										setPhonenum(e.target.value)
										console.log(' : ', phonenum)
									}}
								/>
							</div>
						</div>
						<hr className="divider" />
						{
							!checkNewId || !checkNewPw || validId === false || !studentId || !studentName
								? <Button className="registerBtn" type="submit" disabled></Button>
								: <Button className="registerBtn" type="submit"></Button>
						}
					</form>
				</div>
			</div>
		</div>
	)
}

export default Register;