import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Container from 'react-bootstrap/Container';
import React, { useState, useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import axios from "axios";


function Navbarpage() {

	const navigate = useNavigate()
	const [userData, setUserData] = useState([])

	useEffect(() => {
		axios.get('http://localhost:8081/userpage').then((result) => {
			setUserData(result.data)
		}).catch(() => {
			console.log('failed')
		})
	}, [])

	const logoutBtn = () => {
		axios.get('http://localhost:8081/logout')
			.catch(() => {
				console.log("failed")
			})
		navigate('/', { replace: true })
		window.location.reload(); //page reload   mainpage navbar  ;;
	}

	const handleUserData = (a) => {
		navigate('/approveUser', { state: a })
	}

	return(
		<div className="main">
			<Navbar bg="light" data-bs-theme="light">
				<Container>
					<Navbar.Brand onClick={() => { navigate('/') }}>
						<div className="navtitle">
							<img className="ynclogo2" src="./ynclogo1.ico" />
							 
						</div>
					</Navbar.Brand>
					<Nav className="me-auto">
						<Nav.Link onClick={() => { navigate('/home') }}>
							<div className="navtabs"></div>
						</Nav.Link>
						{
							!userData.id
								? null
								: (userData.id === 'admin'
									? <Nav.Link onClick={() => { navigate('/list') }}>
										<div className="navtabs"> </div>
									</Nav.Link>
									: null)
						}
						{
							!userData.id
								? null
								: (userData.id === 'admin'
									? <Nav.Link onClick={() => { navigate('/approve') }}>
										<div className="navtabs"> </div>
									</Nav.Link>
									: <Nav.Link onClick={() => { handleUserData({ userData }) }}>
										<div className="navtabs"> </div>
									</Nav.Link>)
						}
					</Nav>
					<Navbar.Toggle />
					<Navbar.Collapse className="justify-content-end">
						<Navbar.Text>
							{
								!userData.id
									? <p className="logintab"><div className="navlogin" onClick={() => { navigate('/login') }}> !</div></p>
									: <p className="logintab">
										<div className="navlogin" onClick={() => { navigate('/mypage') }}> {userData.id}!</div>
										<div className="navlogout" onClick={logoutBtn}></div>
										</p>
							}
						</Navbar.Text>
					</Navbar.Collapse>
				</Container>
			</Navbar>
		</div>
	)

}

export default Navbarpage